.container {
	max-width: 1145px;
	margin: 0 auto;
}

.container-max {
	max-width: 1730px;
	margin: 0 auto;
}

.page_control {
	display: flex;
	align-items: center;

	&__prev,
	&__next {
		width: rem(44);
		height: rem(44);

		display: flex;
		align-items: center;
		justify-content: center;

		border: rem(1) solid var(--c-accent);

		&:hover {
			background-color: var(--c-accent);

			svg {
				path {
					stroke: white;
				}
			}
		}

		svg {
			path {
				stroke: var(--c-text);
			}
		}
	}

	&__prev {
		margin-right: rem(10);
	}

	.line {
		margin-left: rem(32);
		margin-right: rem(32);

		flex: 1;
		height: rem(1);
		background-color: #645450;
	}

	&__count {

		.first, .last {
			font-family: var(--f-viaoda);
		}

		.first {
			font-size: rem(48);
			color: var(--c-accent);
		}

		.last {
			font-size: rem(24);
			color: #B5B5B5;

			&::before {
				content: "/";
			}
		}
	}
}

// text

.title-h1 {
	font-size: rem(72);
}

.title-h2 {
	font-size: rem(48);

	span {
		color: var(--c-accent);
	}
}

.title-h3 {
	font-size: rem(40);

	span {
		color: var(--c-accent);
	}
}

.title-h4 {
	font-size: rem(30);

	span {
		color: var(--c-accent);
	}
}

.title-h5 {
	font-size: rem(16);

	span {
		color: var(--c-accent);
	}
}

.font-sup {
	font-family: var(--f-sup);
	font-weight: 500;
	
	font-feature-settings: 'liga'off;
}

// buttons

.button_border_white {
	height: rem(52);
	padding: 0 rem(25);
	border: rem(1) solid white;

	display: flex;
	align-items: center;

	&:hover {
		background-color: white;
		border-color: var(--c-accent);

		span {
			color: var(--c-accent);
		}
	}

	span {
		font-size: rem(12);
		font-weight: 600;
		color: white;
		text-transform: uppercase;
	}
}

.button_accent {
	height: rem(55);
	padding: 0 rem(55);
	background-color: var(--c-accent);

	display: flex;
	align-items: center;

	&:hover {
		background-color: var(--c-accent-h);
	}

	span {
		color: white;
		text-transform: uppercase;
		font-size: rem(12);
		font-weight: 600;
	}
}

// field

.field-set {
	position: relative;

	.icon {
		position: absolute;
		top: rem(1);
		left: rem(1);
	}

	.error-message {
		position: absolute;
		bottom: rem(-22);
		left: 0;

		font-size: rem(14);
		color: #CC7676;
	}

	.field {
		width: 100%;
		padding: 0 rem(20) rem(18) rem(43);
		border-bottom: rem(1) solid #ADADB2;
		background-color: transparent;
		color: var(--c-text);
		font-size: rem(14);

		@include placeholder {
			color: #AAAAAA;
		}
	}
}

.field-set.error {
	.field {
		border-color: #CC7676;
	}
}

@keyframes pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(178, 104, 85, .7);
	}

	70% {
		box-shadow: 0 0 0 rem(15) rgba(178, 104, 85, 0);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(178, 104, 85, 0);
	}
}