.header {
	position: relative;
	height: rem(970);

	background: url(../img/header_bg.jpg) no-repeat top center;
	background-size: cover;

	margin-bottom: rem(107);

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: rem(672);
		background: linear-gradient(136.32deg, #1883D1 30.78%, rgba(41, 148, 226, 0) 62.56%);
		opacity: .5;
		z-index: -1;
	}

	&__top {
		position: absolute;
		top: 0;
		// left: 0;
		width: 100%;
		

		padding-top: rem(40);

		// margin-bottom: rem(159);

		.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
		}

		&_burger {
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			flex: 0 0 rem(20);
			max-width: rem(20);
			height: rem(15);

			margin-right: rem(84);

			.line {
				width: 100%;
				height: rem(1);
				background-color: white;
			}
		}

		&_phone {
			display: flex;
			align-items: center;

			svg {
				path {
					fill: white;
				}
			}

			span {
				color: white;
				font-size: rem(18);
				margin-left: rem(13);
			}
		}

		&_logo {
			margin-left: auto;
			margin-right: auto;

			background: url(../img/logo.svg) no-repeat;
			width: rem(142);
			height: rem(37);
		}

		.button_border_white {
			margin-right: rem(42);
		}
	}

	&__top.header-fixed {
		position: fixed;
		z-index: 15;
		background-color: rgba(249, 246, 244, .9);

		height: rem(82);

		padding-top: 0;

		.container {
			height: 100%;

			.header__top_burger {
				.line {
					background-color: var(--c-accent);
				}
			}

			.header__top_phone {
				svg {
					path {
						fill: var(--c-accent);
					}
				}

				span {
					color: var(--c-text);
				}
			}
			
			.header__top_logo {
				background: url(../img/logo_accent.svg) no-repeat;
			}

			.button_border_white {
				border-color: var(--c-accent);

				span {
					color: #5C5C5C;
				}
			}
		}
	}

	&__bottom {
		padding-top: rem(250);
		

		.container {
			display: flex;
			flex-direction: column;
		}

		&_text {
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: rem(15);

			display: flex;
			flex-direction: column;

			.title {
				
				// font-size: rem(72);
				line-height: 140%;
				letter-spacing: rem(1.4);
				color: white;
				text-transform: uppercase;
			}

			.subtitle {
				// font-size: rem(30);
				color: white;
				text-transform: uppercase;
			}
		}

		&_control {
			max-width: rem(537);

			display: flex;
			justify-content: space-between;

			.button_accent {
				margin-top: rem(38);
			}

			.button_play {
				display: flex;
				flex-direction: column;
				align-items: center;

				&:hover {
					.button_play__overlay {
						animation: pulse 1500ms;
					}
					
				}

				&__overlay {
					width: rem(77);
					height: rem(77);

					display: flex;
					align-items: center;
					justify-content: center;

					background-color: rgba(178, 104, 85, .17);

					border-radius: 50%;
					backdrop-filter: blur(4px);

					margin-bottom: rem(12);

					img {

					}
				}

				span {
					max-width: rem(118);
					font-size: rem(12);
					color: var(--c-accent);
				}
			}
		}
	}
}