.layouts {
	position: relative;
	background-color: #433632;
	padding-top: rem(222);
	padding-bottom: rem(93);

	.title-h2 {
		// text-transform: uppercase;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		color: white;
		margin-bottom: rem(72);
	}

	.overlay {
		position: absolute;
		top: 0;
		height: 100%;
		width: 100%;

		.container {
			position: relative;
			height: 100%;
			width: 100%;
		}
	}

	.slider_nav {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: rem(613);
		right: rem(240);
		z-index: 10;


		width: rem(120);
		height: rem(120);
		border-radius: 50%;

		border: rem(1) solid var(--c-accent);

		&:hover {
			.slider_nav__prev {
				margin-left: rem(-12);
			}

			.slider_nav__next {
				margin-right: rem(-12);
			}
		}

		&__count {
			span {
				font-family: var(--f-viaoda);
			}

			.first {
				font-size: rem(21);
				color: var(--c-accent);
			}

			.last {
				font-size: rem(16);
				color: #727781;

				&::before {
					content: "/";
				}
			}
		}

		&__prev,
		&__next {
			position: absolute;
			top: 50%;
			margin-top: rem(-6);
			transition: var(--transition);
		}

		&__prev {
			left: 0;
			margin-left: rem(12);
		}

		&__next {
			right: 0;
			margin-right: rem(12);
		}
	}

	.slider_layout {

		.swiper-wrapper {
			align-items: center;
		}

		.swiper-slide {
			width: rem(754);
			height: rem(626);

			@include min1440 {
				width: rem(654);
				height: rem(526);
			}

			.layouts__item {
				height: 100%;
				&_bottom {
					display: none;
				}
			}
		}

		.swiper-slide-active {
			height: rem(798);

			@include min1440 {
				height: rem(698);
			}

			.layouts__item {
				height: 100%;
				&_bottom {
					display: flex;
				}
			}
		}
	}

	&__item {
		background-color: white;
		padding: rem(20) rem(40) rem(40) rem(40);

		transition: var(--transition);

		display: flex;
		flex-direction: column;

		&_image {
			height: 100%;
			max-height: rem(523);
			margin: 0 rem(120);

			display: flex;
			align-items: center;
			justify-content: center;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&_bottom {
			margin-top: auto;

			display: flex;
			justify-content: space-between;

			.left {
				display: flex;
				flex-direction: column;

				flex: 0 0 55%;
				max-width: 55%;

				@include min1440 {
					flex: 0 0 50%;
					max-width: 50%;
				}

				.title-h5 {
					margin-bottom: rem(22);
					text-transform: uppercase;
					color: var(--c-text2);
					margin-bottom: rem(20);
				}

				p {
					font-size: rem(14);
					color: var(--c-text2);
				}
			}

			.right {
				display: flex;
				flex-direction: column;

				flex: 0 0 40%;
				max-width: 40%;

				@include min1440 {
					flex: 0 0 45%;
					max-width: 45%;
				}

				> span {
					font-size: rem(16);
					line-height: 120%;
					color: #A5A5A5;
					margin-bottom: rem(18);
				}

				.button_accent {
					display: flex;
					justify-content: center;
					span {
						white-space: nowrap;
					}
				}
			}
		}
	}
}