.map {

	position: relative;

	

	&__top {
		.title-h2 {
			color: var(--c-text);
			margin-bottom: rem(64);
		}

		&_items {
			display: flex;
			justify-content: space-between;
			margin-bottom: rem(70);

			.item {
				display: flex;
				flex-direction: column;

				padding-right: rem(40);
				padding-left: rem(40);

				border-right: rem(1) solid rgba(86,86,92,.2);

				&:last-child {
					padding-right: 0;
					border-right: 0;
				}

				&:first-child {
					padding-left: 0;
				}

				&__first {
					margin-bottom: rem(4);
					white-space: nowrap;
					color: #28282E;
					font-size: rem(24);

					span {
						font-family: var(--f-sup);
						font-size: rem(40);
						
					}
				}

				&__last {
					color: #8E8E93;
					font-size: rem(14);
					line-height: 120%;

					max-width: rem(182);
				}
			}
		}
	}

	&__bottom {
		position: relative;
		height: rem(886);

		&::before {
			content: "";

			position: absolute;
			bottom: rem(-364);
			left: 0;
			z-index: 2;

			width: rem(496);
			height: rem(880);

			background: url(../img/map/left.png) no-repeat;
		}

		.overlay {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&_items {
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			.container {
				position: relative;
				height: rem(886);
			}

			.item1 {
				bottom: rem(786);
				left: rem(474);
			}

			.item2 {
				bottom: rem(683);
				left: rem(983);
			}

			.item3 {
				bottom: rem(496);
				left: rem(564);
			}

			.item4 {
				bottom: rem(395);
				left: rem(549);
			}

			.item5 {
				bottom: rem(255);
				left: rem(651);
			}

			.item6 {
				bottom: rem(28);
				left: rem(738);
			}

			.item {
				position: absolute;
				display: flex;
				flex-direction: column;
				padding: rem(16) rem(24);
				background-color: #433632;

				transition: var(--transition);

				&:hover {
					// padding: rem(16) rem(24) rem(16) rem(52);
					background-color: #C97A66;

					// .item__abs {
					// 	opacity: 0;
					// }

					.item__text,
					.item__subtext {
						color: white;
					}

					// svg {
					// 	opacity: 1;
					// }
				}

				svg {
					position: absolute;
					top: rem(-2);
					left: rem(-20);
					z-index: 2;

					transition: var(--transition);
					opacity: 0;
				}

				&__abs {
					position: absolute;
					top: rem(-12);
					left: rem(-12);
					width: rem(24);
					height: rem(24);
					border-radius: 50%;
					background-color: rgba(255,255,255,.87);

					display: flex;
					align-items: center;
					justify-content: center;
					opacity: 1;
					transition: var(--transition);

					&::before {
						content: '';
						width: rem(16);
						height: rem(16);
						border-radius: 50%;
						background-color: var(--c-accent);
					}
				}

				&__text {
					color: white;
					line-height: 120%;
				}

				&__subtext {
					font-size: rem(13);
					line-height: 120%;
					color: #9196A2;
				}
			}

			.item.active {
				padding: rem(16) rem(24) rem(16) rem(52);
				background-color: #C97A66;

				.item__abs {
					opacity: 0;
				}

				.item__text,
				.item__subtext {
					color: white;
				}

				svg {
					opacity: 1;
				}
			}
		}
	}

}