.footer {

	&__top {
		margin-top: rem(130);
		margin-bottom: rem(150);

		

		.container {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		&_left {
			margin-top: rem(26);
			flex: 0 0 rem(250);
			max-width: rem(250);

			display: flex;
			flex-direction: column;

			.title-h2 {
				margin-bottom: rem(62);
				color: #474A51;
			}

			.block_list {
				display: flex;
				flex-direction: column;
				margin-bottom: rem(44);

				&:last-child {
					margin-bottom: 0;
				}

				&__top {
					margin-bottom: rem(14);
					font-size: rem(14);
					color: #B2B2B2;
				}

				&__bottom {
					font-size: rem(16);
					line-height: 155%;
					color: var(--c-text2);
				}
			}
		}

		&_right {
			flex: 0 0 rem(300);
			max-width: rem(300);

			display: flex;
			flex-direction: column;
			justify-content: center;

			.title {
				max-width: rem(297);
				margin-bottom: rem(40);

				font-size: rem(18);
				line-height: 146%;
				color: var(--c-text2);
			}

			.form {
				display: flex;
				flex-direction: column;

				.field-set {
					margin-bottom: rem(58);
					
				}

				.field-button {
					display: flex;
					flex-direction: column;

					> span {
						margin-top: rem(15);
						max-width: rem(238);
						margin-left: auto;
						margin-right: auto;
						text-align: center;

						font-size: rem(12);
						line-height: 136%;
						color: #ADADB2;
					}
				}
			}
		}
	}

	&__bottom {
		background-color: white;
		padding-top: rem(42);
		padding-bottom: rem(42);

		.logo {
			margin-top: rem(4);
		}

		.container {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}

		.copy {
			text-align: center;

			color: #AAAAAA;
			font-size: rem(12);
		}

		.right {
			color: #AAAAAA;
			font-size: rem(12);
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}

}