.slide_menu {
	position: fixed;
	top: 0;
	left: 0;

	width: 40%;
	height: 100%;

	z-index: 16;

	background-color: #433632;

	overflow-y: auto;

	// display: flex;
	flex-direction: column;
	align-items: flex-end;

	transition: var(--transition);

	transform: translateX(-100%);

	&__close {
		position: absolute;
		top: rem(38);
		right: rem(30);

		display: flex;
		align-items: center;

		span {
			font-size: rem(13);
			color: white;
			text-transform: uppercase;
			margin-right: rem(16);
		}
	}

	&__wrap {
		width: rem(390);

		display: flex;
		flex-direction: column;

		// align-items: flex-end;

		margin-top: rem(58);
	}

	&__logo {
		margin-bottom: rem(100);
	}

	&__nav {

		ul {

			li {
				margin-bottom: rem(50);

				&:last-child {
					margin-bottom: 0;
				}

				a, button {
					position: relative;
					font-size: rem(12);
					text-transform: uppercase;
					color: rgba(255,255,255,.5);
					letter-spacing: rem(2);

					&::before {
						position: absolute;
						top: 50%;
						margin-top: rem(-8);
						left: 0;
						content: "";
						margin-right: rem(3);

						transform: translateX(-16px);
						opacity: 0;

						transition: var(--transition);
					}

					&:hover {
						color: white;

						padding-left: rem(17);

						&::before {
							opacity: 1;
							transform: translateX(0px);
						}
					}
				}

				a.active, button.active {
					color: white;

					padding-left: rem(17);

					&::before {
						opacity: 1;
						transform: translateX(0px);
					}
				}
			}
		}
	}
}

.slide_menu.active {
	transform: translateX(0%);
}