.map_info {
	position: relative;
	background-color: white;
	padding-top: rem(144);

	margin-bottom: rem(150);

	&::before {
		content: "";
		position: absolute;
		top: rem(-318);
		right: 0;
		background: url(../img/section3/right.png) no-repeat;
		width: rem(507);
		height: rem(880);
		z-index: 2;

		@include min1440 {
			right: rem(-120);
		}
	}

	&__top {
		position: relative;
		z-index: 3;
		margin-bottom: rem(73);

		.container {
			position: relative;
		}

		.title-h2 {
			margin-bottom: rem(60);
			color: #474A51;
		}

		.text {
			position: relative;
			max-width: rem(523);
			color: var(--c-text);
			line-height: 140%;
			margin-left: rem(45);

			&::before {
				content: "";
				position: absolute;
				top: rem(-6);
				left: rem(-20);
				background-color: var(--c-accent2);
				height: rem(234);
				width: rem(1);
				border-radius: rem(1);
			}
		}

		.tabs {
			position: absolute;
			top: 100%;
			margin-top: rem(-32);
			right: 0;
			background-color: var(--c-accent);
			width: rem(236);
			height: rem(253);
			padding: rem(52) rem(60) rem(60) rem(60);

			display: flex;
			flex-direction: column;

			.tab {
				position: relative;
				margin-bottom: rem(28);

				font-size: rem(12);
				font-weight: 600;
				color: #E0C6AF;
				text-transform: uppercase;

				display: flex;
				justify-content: flex-start;

				&:last-child {
					margin-bottom: 0;
				}

				&::before {
					content: "";
					position: absolute;
					top: 0;
					left: rem(-18);
					opacity: 0;

					transition: var(--transition);
				}

				&:hover {
					color: white;
					padding-left: rem(18);

					&::before {
						left: 0;
						opacity: 1;
					}
				}
			}

			.tab.active {
				color: white;
				padding-left: rem(18);

				&::before {
					left: 0;
					opacity: 1;
				}
			}
		}
	}

	&__bottom {
		position: relative;

		&::before {
			content: "";
			width: 100%;
			height: rem(105);
			background-color: #F9F6F4;
			position: absolute;
			bottom: 0;
			left: 0;
			// z-index: -1;
		}

		.image {
			position: absolute;
			top: 0;
			height: rem(850);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.container-max {
			.container {
				position: relative;
				height: rem(850);
			}
		}
	}
	
	&__control {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: rem(850);

		.item {
			position: absolute;
			z-index: 2;

			&__button {
				position: absolute;
				bottom: rem(-28);
				right: rem(-28);
				z-index: 3;

				display: flex;
				align-items: center;
				justify-content: center;

				width: rem(56);
				height: rem(56);
				border-radius: 50%;
				background-color: rgba(255, 255, 255, .66);
				backdrop-filter: blur(4px);
				border-radius: 50%;

				&:hover {
					animation: pulse 1500ms;
				}

				.box {
					position: relative;
					width: rem(36);
					height: rem(36);
					border-radius: 50%;
					background-color: var(--c-accent);
				}

				.line {
					position: absolute;
					z-index: 3;
					width: rem(14);
					height: rem(1);
					background-color: white;

					display: block;

					&:first-child {
						top: rem(17);
						left: rem(11);

						transform: rotate(90deg);
					}

					&:last-child {
						top: rem(17);
						left: rem(11);
					}
				}
			}

			&__info {
				position: absolute;
				bottom: 0;
				right: 0;
				width: rem(264);

				// display: flex;
				display: none;
				flex-direction: column;

				&_top {
					position: relative;
					height: rem(189);
					background-color: #433632;

					.swiper-slide {
						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
						}
					}

					.swiper-nav {
						position: absolute;
						z-index: 5;
						bottom: rem(5);
						width: 100%;

						display: flex;
						align-items: center;
						justify-content: center;

						.swiper-pagination-bullet {
							border-radius: 0;
							opacity: 1;
							height: rem(2);
							flex-grow: 1;
							min-width: 1px;
							background-color: rgba(255,255,255,.3);
						}

						.swiper-pagination-bullet-active {
							background-color: var(--c-accent);
						}
					}

					.swiper-page {
						position: absolute;
						top: 50%;
						margin-top: rem(-40);
						width: 100%;
						z-index: 4;

						display: flex;
						justify-content: space-between;

						&__prev, &__next {

							svg {
								circle {
									stroke: white;
								}
							}

							&:hover {
								svg {
									circle {
										stroke: var(--c-accent);
									}
								}
							}
						}

						&__prev {
							position: absolute;
							top: 0;
							left: rem(-40);
						}

						&__next {
							position: absolute;
							top: 0;
							right: rem(-40);
						}
					}
				}

				&_bottom {
					display: flex;
					flex-direction: column;
					background-color: #433632;
					padding: rem(33) rem(30) rem(42) rem(30);

					.title-h4 {
						margin-bottom: rem(4);
						color: white;
						font-size: rem(21);
					}

					.text {
						font-size: rem(14);
						line-height: 152%;
						color: white;
					}
				}
			}

		}

		.item.active {
			.item__info {
				display: flex;
			}

			.item__button {
				.box {
					background-color: white;

					.line {
						background-color: var(--c-text);

						top: rem(18);

						&:first-child {
							transform: rotate(45deg);
						}

						&:last-child {
							transform: rotate(-45deg);
						}
					}
				}
			}
		}

		.item1 {
			bottom: rem(459);
			left: rem(287);

			// @include min1440 {
			// 	bottom: rem(520);
			// }
		}

		.item2 {
			bottom: rem(357);
			left: rem(925);

			// @include min1440 {
			// 	bottom: rem(438);
			// 	left: rem(872);
			// }
		}

		.item3 {
			bottom: rem(192);
			left: rem(128);

			// @include min1440 {
			// 	bottom: rem(320);
			// 	left: rem(240);
			// }
		}

		.item4 {
			bottom: rem(154);
			left: rem(671);

			// @include min1440 {
			// 	bottom: rem(318);
			// }
		}
	}
}

