.investment {
	position: relative;
	padding-top: rem(154);
	padding-bottom: rem(77);
	
	background: url(../img/investment/bg.jpg) no-repeat top center;
	
	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(50, 41, 38, .97);
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.list {
		display: flex;
		padding: rem(16) 0;
		border-top: rem(1) solid #6E7380;
		
		&__count {
			font-size: rem(16);
			color: white;
			font-family: var(--f-viaoda);
			flex: 0 0 rem(50);
			max-width: rem(50);
		}

		&__text {
			font-size: rem(16);
			font-weight: 500;
			line-height: 152%;
			color: #B5B5B5;
		}

	}

	&__left,
	&__right {
		position: relative;
		z-index: 2;
	}

	&__left {
		margin-top: rem(36);
		flex: 0 0 rem(415);
		max-width: rem(415);
	
		display: flex;
		flex-direction: column;

		.swiper_left {
			width: 100%;
		}

		.title-h2 {
			color: white;
			margin-bottom: rem(48);
		}

		.title {
			margin-bottom: rem(8);
			font-size: rem(16);
			font-weight: 500;
			color: white;
			line-height: 152%;
			display: block;
		}

		.text {
			font-size: rem(16);
			color: #BCBCC1;
			line-height: 152%;
			margin-bottom: rem(40);
		}

		.page_control {
			&__prev,
			&__next {
				svg {
					path {
						stroke: white;
					}
				}
			}
		}
	}

	&__right {
		margin-left: rem(82);
		flex: 1;
		min-width: 0;

		

		.swiper_right {
			width: 100%;
		}

		.slide1 {
			flex: 0 0 rem(632);
			max-width: rem(632);
		}

		.slide2 {
			margin-top: rem(188);
			display: flex;
			flex-direction: column;
			
			.title {
				margin-bottom: rem(11);
				font-size: rem(16);
				font-weight: 500;
				color: white;
				line-height: 152%;
				max-width: rem(210);
			}

			.text {
				font-size: rem(16);
				color: #BCBCC1;
				line-height: 152%;
				max-width: rem(530);
				// margin-bottom: rem(40);
			}
		}

		.slide3 {
			img {
				margin-bottom: rem(42);
			}

			.list {
				max-width: rem(348);
			}
		}
	}
}