:root {
	--f-default: 'Inter', sans-serif;
	--f-sup: 'FoglihtenNo06', sans-serif;
	--f-viaoda: 'Viaoda Libre', cursive;

	--c-accent: #B26855;
	--c-accent2: #C68574;
	--c-accent-h: #D7816B;
	--c-text: #474A51;
	--c-text2: #5C5C5C;

	--transition: 250ms ease;
}

* {
	outline: none;
	border: 0;
	margin: 0;
	padding: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

a, button {
	transition: var(--transition);

	* {
		transition: var(--transition);
	}
}

li {
	list-style: none;
}

body {
	position: relative;
	font-size: rem(16);
	font-family: var(--f-default);
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;

	background-color: #F9F6F4;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}