.info {
	position: relative;
	z-index: 3;
	margin-bottom: rem(120);

	&::before {
		content: "";
		position: absolute;
		top: rem(-160);
		left: 0;

		width: rem(342);
		height: rem(636);

		background: url(../img/section2/left.png) no-repeat top center;

		@include min1440 {
			top: rem(-240);
			// left: rem(-64);
		}
	}

	.container {
		display: flex;
		// flex-wrap: wrap;

		// justify-content: space-between;
	}

	&__wrap_tab {
		display: none;
	}

	&__wrap_tab.active {
		display: flex;
	}

	.tab_1 {
		margin-left: rem(77);
	}

	.tab_2 {
		margin-left: rem(34);
	}

	.tab_3 {
		margin-left: rem(68);
	}

	&__tab {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		align-self: flex-start;

		// margin-right: rem(77);
		margin-left: rem(6);

		padding-left: rem(37);

		border-left: rem(1) solid #E1C9B3;

		margin-top: rem(90);

		.item {
			position: relative;
			margin-bottom: rem(24);
			height: rem(61);

			display: flex;
			align-items: center;

			&:last-child {
				margin-bottom: 0;
			}

			&:hover {
				.text {
					color: var(--c-accent);
				}
			}

			.line {
				position: absolute;
				top: 0;
				left: rem(-39);
				height: 100%;
				width: rem(2);
				background-color: var(--c-accent);

				opacity: 0;

				&::before {
					content: "";
					position: absolute;
					top: 50%;
					margin-top: rem(-6);
					left: 0;

					width: 0;
					height: 0;

					
					border: rem(6) solid transparent;

					border-right: 0;
					border-left-color: var(--c-accent);
				}
			}

			.text {
				font-size: rem(12);
				letter-spacing: rem(5);
				text-transform: uppercase;
				color: var(--c-text);
			}
		}

		.item.active {

			.line {
				opacity: 1;
			}
			
			.text {
				color: #B26855;
				font-weight: 600;
				font-size: rem(14);
			}
		}
	}

	&__center {
		flex: 0 0 rem(420);
		max-width: rem(420);
	}

	&__text {
		margin-top: rem(42);
		flex: 0 0 rem(373);
		max-width: rem(373);
		// flex-grow: 1;
		min-width: 0;

		display: flex;
		flex-direction: column;

		margin-left: rem(70);

		.swiper {
			width: 100%;
		}

		&_head {
			margin-bottom: rem(52);
			display: flex;
			flex-direction: column;

			.subtitle {
				font-size: rem(10);
				line-height: 140%;
				text-transform: uppercase;
				color: var(--c-accent2);

				margin-bottom: rem(17);
			}

			.title-h2 {
				color: var(--c-text);
			}
		}

		&_p {
			position: relative;
			margin-bottom: rem(83);

			margin-left: rem(30);

			&::before {
				content: "";
				position: absolute;
				top: rem(10);
				left: rem(-132);
				background-color: var(--c-accent2);
				height: rem(1);
				width: rem(114);
			}

			p {
				color: var(--c-text);
				line-height: 140%;
				margin-bottom: rem(28);

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.page_control {
			margin-right: rem(48);
		}
	}

	

	&__list {

		flex: 0 0 rem(754);
		max-width: rem(754);

		// @include min1440 {
		// 	flex: 0 0 rem(674);
		// 	max-width: rem(674);
		// }

		&_head {
			.title-h2 {
				margin-bottom: rem(24);
				color: var(--c-text);
			}

			p {
				color: var(--c-text2);
				line-height: 140%;
				max-width: rem(288);
			}
		}

		&_items {
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			height: rem(425);

			// @include min1440 {
			// 	height: rem(524);
			// }
		}

		&_item {
			display: flex;
			padding-top: rem(12);
			margin-bottom: rem(32);
			max-width: rem(362);
			border-top: rem(1) solid #DAD5D1;

			// @include min1440 {
			// 	max-width: rem(282);
			// }

			&:first-child {
				margin-right: rem(30);
				border-top: 0;

				.text {
					max-width: rem(288);
				}
			}

			&:nth-child(2) {
				margin-right: rem(30);
			}

			&:nth-child(3) {
				margin-right: rem(30);
			}

			&:nth-child(4) {
				margin-bottom: 0;
				margin-right: rem(30);
			}

			&:nth-child(5) {
				margin-top: rem(33);

				// @include min1440 {
				// 	margin-top: rem(80);
				// }
			}

			&:last-child {
				margin-bottom: 0;
			}

			.count {
				margin-right: rem(20);
				font-family: var(--f-sup);
				color: var(--c-text2);
			}

			.text {
				color: var(--c-text2);
				font-size: rem(16);
			}
		}

	}

	&__right_image {
		flex: 0 0 rem(425);
		max-width: rem(425);
		margin-left: rem(48);
		// height: rem(5);

		@include min1440 {
			margin-left: rem(24);

			flex: 0 0 rem(362);
			max-width: rem(362);
		}
	}

	&__left_image {
		flex: 0 0 rem(598);
		max-width: rem(598);

		margin-right: rem(46);

		@include min1440 {
			flex: 0 0 rem(498);
			max-width: rem(498);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__blocks {
		display: flex;
		flex-direction: column;

		justify-content: center;

		.title-h2 {
			color: var(--c-text);
			margin-bottom: rem(81);
		}

		.items {
			display: flex;
			flex-wrap: wrap;

			.item {
				flex: 0 0 50%;
				max-width: 50%;

				margin-bottom: rem(42);

				&:nth-child(3), &:last-child {
					margin-bottom: 0;
				}

				&__top {
					.text {
						font-family: var(--f-sup);
						font-size: rem(36);
						color: var(--c-text);

						span {
							margin-left: rem(4);
							font-size: rem(14);
							text-transform: uppercase;
						}
					}
				}

				&__bottom {
					span {
						font-size: rem(14);
						color: #AAAAAA;
					}
				}
			}
		}
	}
}