.message {
	margin-bottom: rem(120);

	.title-h2 {
		max-width: rem(700);
		margin-bottom: rem(42);
		color: #474A51;
	}

	.text {
		margin-bottom: rem(44);
		font-size: rem(18);
		line-height: 146%;
		color: var(--c-text2);
	}

	.form {
		display: flex;
		align-items: center;
		// justify-content: space-between;

		.field-set {
			flex: 0 0 rem(329);
			max-width: rem(329);

			margin-right: rem(42);
		}

		.field-button {
			display: flex;
			flex-direction: column;
			align-items: center;

			flex: 0 0 rem(294);

			button {
				width: 100%;
			}

			> span {
				margin-top: rem(10);
				font-size: rem(12);
				line-height: 138%;
				color: #ADADB2;
				text-align: center;
			}
		}
	}

}