.modal_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(50, 41, 38, .82);
	z-index: 100;

	display: flex;
	align-items: center;
	justify-content: center;

	display: none;

	.modal_wrap {
		position: relative;
		width: rem(620);
		background-color: white;

		display: flex;
		flex-direction: column;

		.modal_close {
			position: absolute;
			top: rem(34);
			right: rem(34);
		}

		.title-h4 {
			color: #4A4D55;
			margin-bottom: rem(14);
		}

		.text {
			font-size: rem(14);
			line-height: 130%;
			color: #474A51;
			margin-bottom: rem(40);
		}


	}

	.modal_wrap_success {
		padding-top: rem(140);
		padding-bottom: rem(84);

		align-items: center;
		justify-content: center;

		.text {
			max-width: rem(293);
			text-align: center;
		}
		
	}
}

.modal_overlay.active {
	display: flex;
}

.modal_phone {
	.modal_wrap {
		
		padding: rem(100) rem(75) rem(90) rem(110);

		.title-h4 {
			margin-bottom: rem(34);
		}

		.text {
			margin-bottom: rem(72);
			padding-right: rem(35);
		}

		.form {
			padding-right: rem(35);
		}

		.field-set {
			margin-bottom: rem(45);

			.field {
				font-size: rem(16);
			}
		}
		
		.field-button {
			button {
				width: 100%;
				height: rem(67);
				justify-content: center;
				margin-bottom: rem(16);
			}

			.text {
				font-size: rem(12);
				line-height: 140%;
				color: #8E8E93;

				span {
					text-decoration: underline;
				}
			}

		}
	}
}