.gallery {
	background-color: #433632;
	padding-top: rem(92);
	margin-bottom: rem(132);

	&__top {
		margin-bottom: rem(62);

		display: flex;
		align-items: center;
		justify-content: space-between;

		.title-h2 {
			color: white;
		}

		.page_control {

			&__prev,
			&__next {
				svg {
					path {
						stroke: white;
					}
				}
			}

			.line {
				width: rem(170);
			}
		}
	}

	&__bottom {
		height: rem(622);

		.swiper {
			margin-bottom: rem(-22);
			padding-bottom: rem(22);
		}

		.swiper-slide {
			position: relative;
			width: 1146px;
			height: rem(622);

			.text {
				position: absolute;
				bottom: rem(-22);
				right: rem(22);

				background-color: rgba(176, 104, 85, .96);
				padding: rem(32);
				width: rem(273);

				font-size: rem(14);
				line-height: 152%;
				color: white;

				opacity: 0;
			}

			&::before {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(134,112,106,.6);

				opacity: 1;

				transition: var(--transition);
			}
		}

		.swiper-slide-active {
			&::before {
				opacity: 0;
			}

			.text {
				opacity: 1;
			}
		}

		a {
			width: 100%;
			height: 100%;
			display: block;
		}

		img {
			width: 100%;
			height: rem(622);
			object-fit: cover;
		}
	}
}